.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    margin-top: 0;
    background-color: #ffffff !important;
    z-index: 999;
    border-bottom: 1px solid #ddd;
    box-shadow: 0px 1px 3px 0px #d2d0d0dd;
  }

  .sticky + .content {
    padding-top: 102px;
  }
