.form-bg{
  border: 1px solid #dcd4d4;
  padding: 40px;
  border-radius: 5px;
  background: #fff;
  margin-top: 0px;
  width: 98%;
}
.font-30{
  font-size: 35px;
  line-height: 40px;
  color: #333;
  text-transform: capitalize;
}

.form-row {
  margin-left: -15px;
  margin-right: -15px;
}
.form-row .form-group {
  padding: 10px 5px;
  align-self: flex-end;
}
.form-group {
  position: relative;
  padding-top: 15px;
  margin-bottom: 0px;
}
.form-control {
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 16px 20px;
  background-color: transparent;
  position: relative;
  z-index: 2;
  font-size: 0.9rem;
  color: #333333;
  margin-top: 2px;
}
.form-control:not(textarea) {
  height: 46px;
}
.form-control + label {
  position: absolute;
  z-index: 1;
  top: 12px;
  margin: 0;
  transition: all 0.2s ease-in-out;
  pointer-events: none;
  left: 10px;
  right: 10px;
  line-height: 42px;
}
.form-control:focus {
  box-shadow: none;
  background-color: transparent;
  border: 1px solid #ddd;
}
.form-control:focus + label {
  color: #969696;
  top: 10px;
}
.form-control:focus + label, .form-control.has-value + label {
  font-size: 0.75rem;
  top: 2px;
  color: #969696;
}
.no-js .form-control + label {
  font-size: 0.75rem;
  top: 0;
}
.form-row .form-control + label {
 
    left: 25px;
    right: 25px;
    position: absolute;

}
.form-row .line {
  left: 15px;
  right: 15px;
  top: 10px;  
}


.form-control:focus ~ .line:after {
  transform: scaleX(1);
}
select.form-control:not([size]):not([multiple]) {
  height: 45px;
  padding: 0 15px;
  font-size: 16px;
}

.checkbox-sec [type="radio"]:not(:checked) + label:before {
  top: 0;
  width: 19px;
  height: 19px;
  /* border: 1px solid #6c757d; */
  border-radius: 25px;
  box-shadow: 0 1px 5px 2px #ddd;
}

.checkbox-sec [type="radio"]:not(:checked) + label:after {
  top: 0;
  width: 19px;
  height: 19px;
  border: 1px solid #6c757d;
  z-index: 0;
  border-radius: 25px;
  /* Checked styles */
}

.checkbox-sec [type="radio"]:checked + label:before {
  top: 2px;
  left: 2px;
  width: 6px;
  height: 12px;
  border: 1px solid transparent;
  border-right-color: red;
  border-bottom-color: red;
  -webkit-transform: rotateZ(37deg);
  transform: rotateZ(37deg);
  -webkit-transform-origin: 20% 40%;
  transform-origin: 100% 100%;
  box-shadow: 0 1px 5px 2px #ddd;
}

.checkbox-sec [type="radio"]:checked + label:after {
  top: 0;
  width: 19px;
  height: 19px;
  border: 1px solid red;
  z-index: 0;
  border-radius: 25px;
  /* disabled checkbox */
}

.checkbox-sec [type="radio"]:disabled:not(:checked) + label:before, 
.checkbox-sec [type="radio"]:disabled:checked + label:before {
  top: 0;
  box-shadow: none;
  background-color: #444;
  width: 19px;
  height: 19px;
  border: 3px solid #444;
  -webkit-transform: rotateZ(0deg);
  transform: rotateZ(0deg);
}

.checkbox-sec [type="radio"]:disabled + label {
  color: #555;
}

.checkbox-sec [type="radio"]:disabled:not(:checked) + label:hover:before {
  border-color: red;
}
.checkbox-sec [type=checkbox]:checked + label,
.checkbox-sec [type=checkbox]:not(:checked) + label {
    height: auto;
    overflow: initial;
    font-size: 14px;
    color: #333333;
    /* display: flex; */
    position: relative;
    z-index: 0;
}
.checkbox-sec [type=checkbox]:not(:checked) + label a, a, a:hover{
  color: #f13d3d;
  text-decoration: none;
}


.checkbox-sec [type="checkbox"]:not(:checked), .checkbox-sec [type="checkbox"]:checked {
    position: absolute;
    left: -9999px;
}

.checkbox-sec [type="checkbox"]:not(:checked) + label, .checkbox-sec [type="checkbox"]:checked + label {
    position: relative;
    overflow: hidden;
    padding-left: 35px;
    cursor: pointer;
    display: inline-block;
    height: 25px;
    line-height: 17px;
    /* webkit (safari, chrome) browsers */
    -webkit-user-select: none;
    /* mozilla browsers */
    -moz-user-select: none;
    /* webkit (konqueror) browsers */
    -khtml-user-select: none;
    /* IE10+ */
    -ms-user-select: none;
    /* checkbox aspect */
}

.checkbox-sec [type="checkbox"] + label:before, .checkbox-sec [type="checkbox"] + label:after {
    content: '';
    position: absolute;
    left: 0;
    z-index: 1;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    /* Unchecked styles */
}

.checkbox-sec [type="checkbox"]:not(:checked) + label:before {
    top: 0;
    width: 19px;
    height: 19px;
    border: 2px solid #ddd;
}

.checkbox-sec [type="checkbox"]:not(:checked) + label:after {
    top: 0;
    width: 19px;
    height: 19px;
    border: 2px solid red;
    z-index: 0;
    /* Checked styles */
}

.checkbox-sec [type="checkbox"]:checked + label:before {
    top: 2px;
    left: 2px;
    width: 6px;
    height: 12px;
    border: 1px solid transparent;
    border-right-color: red;
    border-bottom-color: red;
    -webkit-transform: rotateZ(37deg);
    transform: rotateZ(37deg);
    -webkit-transform-origin: 20% 40%;
    transform-origin: 100% 100%;
}

.checkbox-sec [type="checkbox"]:checked + label:after {
    top: 0;
    width: 19px;
    height: 19px;
    border: 2px solid red;
    z-index: 0;
    /* disabled checkbox */
}

.checkbox-sec [type="checkbox"]:disabled:not(:checked) + label:before, .checkbox-sec [type="checkbox"]:disabled:checked + label:before {
    top: 0;
    box-shadow: none;
    background-color: #444;
    width: 19px;
    height: 19px;
    border: 3px solid #444;
    -webkit-transform: rotateZ(0deg);
    transform: rotateZ(0deg);
}

.checkbox-sec [type="checkbox"]:disabled + label {
    color: #555;
}

.checkbox-sec [type="checkbox"]:disabled:not(:checked) + label:hover:before {
    border-color: red;
}

